
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import Vue from 'vue'
import http from '@/http'

Vue.use(ToastPlugin)

export default {
components: {
Cell,
XInput,
Group,
XButton,
Toast
},
methods: {
setPwd () {
if (this.pwd.pwd1 === this.pwd.pwd2) {
http('user-center-server/secure/password/reset', {oldPassword: this.pwd.pwd3, newPassword: this.pwd.pwd1}).then((res) => {
if (res === true) {
this.$vux.toast.text('设置密码成功')
this.$router.push('/UserCenter')
}
})
} else {
this.$vux.toast.text('两次密码输入不一致！')
}
},
change () {
if (this.pwd.pwd1 && this.pwd.pwd2) {
this.setBtnDisabled = false
} else {
this.setBtnDisabled = true
}
}
},

mounted: function () {
},
data () {
return {
showPositionValue: true,
setBtnDisabled: true,
pwd: {
pwd1: '',
pwd2: '',
pwd3: ''
}
}
}
}



